.has-border-bottom {
    .staff-list {
        .staff-listing {
            margin-bottom: -2px;
        }
    }
}

.staff-list {
    .no-category-up {
        position: relative;

        &:before {
            content: '';
            background-color: var(--gb_base_brown);
            top: -2px;
            height: 2px;
            left: 0;
            margin-left: calc(-50vw + 50%);
            position: absolute;
            width: 1000%;
        }
    }

    .tabbed-block-content {
        .tabs {
            margin-left: calc(-50vw + 50%);
            padding-left: 24px;
            padding-right: 24px;
            width: 100vw;

            @media (min-width: 1920px) {
                margin-left: 0;
                width: 100%;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    .staff-member {
        display: none;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;

        &.fade-in {
            opacity: 1;
        }
    }

    .no-staff-message {
        display: none;
    }

    .tab-link {
        cursor: pointer;
        transition: all 0.3s ease;
    }

    .tab-content {
        margin-bottom: -2px;
    }

    .staff-listing {
        display: grid;
        gap: 0;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        margin-left: calc(-50vw + 50%);
        width: calc(100vw + 2px);

        @media (min-width: 1580px) {
            grid-template-columns: repeat(4, minmax(0, 1fr));
        }

        @media (max-width: 767px) {
            grid-template-columns: repeat(2, minmax(0, 1fr));
        }

        @media (max-width: 319px) {
            grid-template-columns: repeat(1, minmax(0, 1fr));
            width: 100vw;
        }

        .card-item {
            border-bottom: 2px solid var(--gb_base_brown);
            border-right: 2px solid var(--gb_base_brown);
            text-decoration: none;
            display: flex;
            flex-direction: column;

            @media(max-width: 319px) {
                border-right: 0;
            }

            // &:nth-child(even) {
            //     @media (max-width: 767px) {
            //         border-right: none;
            //     }
            // }

            // &:nth-child(4n) {
            //     @media (min-width: 1580px) {
            //         border-right: none;
            //     }
            // }

            // &:nth-child(3n) {
            //     @media only screen and (min-width:768px) and (max-width:1580px) {
            //         border-right: none;
            //     }
            // }


            &.bg-lilac {
                background-color: var(--gb_primary_lilac);
            }

            &.bg-lemon-yellow {
                background-color: var(--gb_primary_lemon_yellow);
            }

            &.bg-sky-blue {
                background-color: var(--gb_primary_sky_blue);
            }

            &.bg-lime-green {
                background-color: var(--gb_primary_lime_green);
            }

            &.bg-butter-yellow {
                background-color: var(--gb_primary_butter_yellow);
            }

            &.bg-sunflower-yellow {
                background-color: var(--gb_primary_sunflower_yellow);
            }

            &.bg-primary-pink {
                &:before {
                    background-color: var(--gb_primary_pink);
                }
            }

            &:hover {
                background-color: var(--gb_primary_butter_yellow);
            }

            .card-body {
                padding-left: 80px;
                padding-right: 80px;

                @media (max-width: 1199px) {
                    padding-left: 48px;
                    padding-right: 48px;
                }

                @media (max-width: 991px) {
                    padding-left: 24px;
                    padding-right: 24px;
                }

                @media (max-width: 374px) {
                    padding-left: 12px;
                    padding-right: 12px;
                }
            }

            .card-img {
                margin-top: auto;

                img {
                    display: block;
                    margin: 0 auto;
                    max-width: clamp(187px, calc(11.6875rem + ((1vw - 7.68px) * 50.3436)), 480px);
                    width: 100%;
                    max-height: clamp(147px, calc(9.1875rem + ((1vw - 7.68px) * 39.3471)), 376px);
                    object-fit: contain;
                }
            }
        }
    }
}